
#loaderPopup{position:fixed;width:100%;height:100%;z-index:1000;top:0;left:0;background:#fff
}
.overlay{opacity:0.9
}
.popupContainer{position:fixed;left:50%;top:5%;margin-left:-193px;background:#fff;padding:5px;z-index:1001;min-width:385px
}
.popupContainer h4{background:#f3f3f3;padding:0 10px
}
.popupContainer .popupContent{height:auto;overflow-y:scroll
}
.popupContainer .loadLine{border-bottom:1px solid #f3f3f3
}
.popupContainer .key,.popupContainer .value,.popupContainer .button{display:block;margin:0 5px;float:left;overflow-wrap:break-word
}
.popupContainer .value{width:180px
}
.popupContainer .key{width:85px
}
.popupContainer .button{padding:0 10px
}
.popupContainer .cancel{display:block;margin:0 5px
}
.fade_popup-enter-active,.fade_popup-leave-active{transition:opacity .3s;opacity:1
}
.fade_popup-enter,.fade_popup-leave-to{opacity:0
}
.fade_overlay-enter-active,.fade_overlay-leave-active{transition:opacity .3s;opacity:0.9
}
.fade_overlay-enter,.fade_overlay-leave-to{opacity:0
}



