
#loaderPopup{position:fixed;width:100%;height:100%;z-index:1000;top:0;left:0;background:#f3f3f3
}
.overlay{opacity:0.9
}
.popupContainerSave{position:fixed;left:50%;top:5%;margin-left:-193px;background:#fff;padding:5px;z-index:1001;min-width:385px
}
.popupContainerSave h4{background:#f3f3f3;padding:0 10px
}
.popupContainerSave .nameInput{width:285px
}
.popupContainerSave .cancel{display:block;margin:0 5px
}
.popupContainerSave .disabled{cursor:default;border:#f3f3f3 1px solid;color:#f3f3f3
}
.popupContainerSave .disabled:hover{cursor:default;border:#f3f3f3 1px solid;color:#f3f3f3
}
.fade_popup-enter-active,.fade_popup-leave-active{transition:opacity .3s;opacity:1
}
.fade_popup-enter,.fade_popup-leave-to{opacity:0
}
.fade_overlay-enter-active,.fade_overlay-leave-active{transition:opacity .3s;opacity:0.9
}
.fade_overlay-enter,.fade_overlay-leave-to{opacity:0
}



