
#tickets-wrapper{position:static
}
#cta{text-align:center;margin:-50px 0 15px 0
}
#cta .disabled{opacity:0.5;cursor:default
}
#cta .disabled:hover{color:#FFF;background-color:#33C3F0;border-color:#33C3F0
}
#tickets h1{font-size:30px
}
#tickets h2{font-size:18px
}
#tickets .ticket{margin-bottom:30px;background:#f3f3f3;padding:20px;border-radius:4px
}
#tickets .number{font-weight:bold
}
#tickets .scenario{font-style:italic;background:#fff
}
#tickets .center{text-align:center
}
#tickets .label{display:inline-block;background:#eee;padding:4px 10px;margin:0 10px;text-transform:uppercase;font-size:12px;border-radius:4px;font-style:normal;font-weight:bold
}
#tickets .unit{color:black;font-size:11px
}
#tickets .incatious{color:red
}
#tickets .three{color:#f80
}
#tickets .safe{color:#0a0
}
#tickets .no-montecarlo{background:#f00;color:white
}
#tickets .attribute{background:#0a0;color:white
}



